body {
  background: linear-gradient(135deg, #241e1e, #1b172e, #251e1e, #1f1e33);
  background-size: 400% 400%;
  animation: gradientMove 20s ease infinite;
  color: white;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@keyframes gradientMove {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.sidebar {
  margin-top: 125px;
  position: fixed;
  height: 50vh;
  width: 125px;
  padding: 20px;
  background-color: linear-gradient(180deg, #6b6b6b, #423636);
  gap: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.5);
  align-items: left;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar button {
  text-align: center;
  background: transparent;
  border: 2px solid rgb(56, 56, 56);
  box-shadow: 2px 0 5px  rgba(0,0,0,0.5);
  color: white;
  border-radius: 5px;
  font-size: 18px;
  text-align: left;
  cursor: pointer;
  padding: 10px 5px;
  width: 100%;
  font-weight: bold;
  transition: all 0.3s ease;
}

.sidebar button:hover {
  background-color: #2a5298;
  border-radius: 5px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin-top: 100px;
}

.name {
  font-size: 50px;

}

.subtitle {
  color: rgb(167, 167, 167);
  font-size: 16px;
  margin-top: -35px;
}

.about {
  max-width: 1000px;
  margin: 10px auto;
  padding: 20px;
  text-align: left;
  line-height: 1.6;
}

.about h2 {
  font-size: 1.5rem;
}

.about p {
  font-size: 16px;
}

.languages {
  max-width: 1000px;
  margin: -50px auto;
  padding: 20px;
  text-align: left;
}

.languages h3 {
  font-size: 1.5rem;
}

.languages h4 {
  font-size: 1.5rem;
}

.languages ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.languages li {
  background: linear-gradient(-45deg, #1e3c72, #2a5298, #1e3c72, #2a5298);
  background-size: 400% 400%;
  animation: gradientBG 10s ease infinite;
  background-color: #ffffff22;
  padding: 7px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 125px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  margin-right: 15px;
  font-size: 1.2rem;
  color: white;
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.languages li:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.languages img {
  width: 30px;
  height: 30px;
  margin-right: 1px;
}

.resume-button {
  display: inline-block;
  padding: 10px 20px;
  background-color: rgb(83, 83, 83);
  color: white;
  text-decoration: none;
  font-weight: bold;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.resume-button:hover {
  background-color:#363636;
}

.cover h5 {
  font-size: 1.5rem;
  text-align: left;
  margin-left: 450px;
}

